/* Landscape phone to portrait tablet */
/* Portrait tablet to landscape and desktop */
/* Large desktop */
.content {
  background-color: var(--divider);
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.content .sidebar {
  flex: 0 0 25rem;
  padding: 0 2rem 2rem 2rem;
}
.content .sidebar select {
  width: 100%;
  margin-bottom: 2rem;
  border-radius: 0.3rem;
  background: white;
  border: 0;
  outline: 0;
  padding: 1rem;
  box-shadow: var(--box-shadow);
}
.content .sidebar .categories {
  padding: 2rem;
  background: white;
  border-radius: 0.3rem;
  box-shadow: var(--box-shadow);
}
.content .sidebar .categories li {
  margin: 1rem;
  transition: color 0.2s;
  cursor: pointer;
}
.content .sidebar .categories li:hover {
  color: var(--primary);
}
.content .sidebar .categories li:hover strong {
  color: var(--primary);
}
.content .sidebar .categories .active {
  color: var(--primary);
  font-weight: 700;
}
.content .sidebar .categories .active strong {
  color: var(--primary);
}
.content .articles-container {
  max-width: 80rem;
  flex: 1;
  margin: 0 0 10rem 0;
}
.content .articles-container .article {
  background-color: white;
  padding: 0 5rem;
  border-radius: 1rem;
  box-shadow: var(--box-shadow);
  display: flex;
  margin-bottom: 7rem;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 480px) {
  .content .articles-container .article {
    padding: 0 2rem;
  }
}
.content .articles-container .article img {
  height: 10rem;
  width: 10rem;
  border-radius: 50%;
  margin-top: -5rem;
}
.content .articles-container .article h2 {
  margin-top: 2rem;
  margin-bottom: 0;
}
.content .articles-container .article .article-author {
  color: var(--hint);
  margin-bottom: 3rem;
}
.content .articles-container .article .article-content {
  max-width: 75rem;
  white-space: pre-line;
}
.content .articles-container .article .article-actions {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 2rem 0;
  margin-top: 2rem;
  border-top: 1px solid var(--divider);
}
.content .articles-container .article .article-actions .btn {
  margin-left: 1rem;
